.section-main-block {
  $origin: &;
  display: flex;
  justify-content: space-between;
  margin-bottom: -40px;
  border: 1px;
  position: relative;
  height: 1024px;

  &__title-container {
    position: absolute;
    top: 0;
    left: 0;

    padding-top: 210px;
    padding-bottom: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;

    height: 100%;
    z-index: 1;

    &__content-first {
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-left: 125px;
      margin-right: -30px;

      // min-width: 982px;
      // min-width: 70%;

      &__title {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: clm_px3(70, 80, $maxWindowWidth, $desk);
        line-height: 101%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #62500f;
        text-wrap: nowrap;

        & > b {
          font-weight: 500;
          color: var(--primary);
        }
      }

      &__text {
        width: 100%;
        max-width: 500px;

        font-family: var(--second-family);
        font-weight: 400;
        font-size: px-to-rem(24);
        line-height: 130%;
        color: #5d5955;
      }

      &__button {
        width: 100%;
        max-width: 500px;
      }
    }

    &__content-second {
      position: relative;
      display: grid;
      grid-template-columns: max-content 1fr;
      gap: 20px;
      width: 762px;

      &__title-mini {
        padding-left: 20px;
        margin-bottom: -10px;
      }

      &__video {
        width: 292px;
        height: 170px;
      }
    }
  }

  /* --------------------------- */

  &__content-container {
    position: absolute;
    top: 0;
    right: -10%;

    margin-top: -125px;

    &__wrapper .main-photo__block {
      z-index: 1;
    }

    &__photo {
      @include size(524px);
      position: relative;
      overflow: hidden;
      border-radius: 100%;
      background: #fff0e2;

      -webkit-backdrop-filter: blur(20px);
      backdrop-filter: blur(20px);
      box-shadow: 0 0 15px 0 rgb(180 174 167 / 20%);
      z-index: 2;
    }

    &__card-photo {
      @include shadow;
      width: 388px;
      position: absolute;
      border-radius: 30px;
      overflow: hidden;
      left: -296px;
      bottom: -56px;

      & > * {
        @include size(100%);
        align-items: end;
      }

      & .card-block-4 {
        align-items: center;
      }

      & .card-block-4__container {
        // padding-right: 64px;
      }
    }
  }

  // Effects

  &--blue {
    #{$origin}__title-container {
      &__content-first {
        &__text {
          color: var(--text-piro-title);
        }
  
        &__title {
          color: var(--text-piro-title);
  
          & > b {
            color: var(--text-piro-accent);
          }
        }
      }
    }

    #{$origin}__content-container {
      &__circle {

      }

      &__photo {
        @include flex-center;
        @include fluid-property-DF(width,  596, 596, 400, 400, 300);
        @include fluid-property-DF(height, 596, 596, 400, 400, 300);

        overflow: inherit;
        background: #cae5f7;

        &__image {
          @include fluid-property-DF(width,  596, 596, 400, 400, 300);
          @include fluid-property-DF(height, 596, 596, 400, 400, 300);
        }
      }

      &__wrapper {
        @include toLaptop {
          transform: translateX(20px);
        }

        @include toTablet {
          transform: translateX(0);
        }

        @include toPhone {
          transform: translateX(10px);
        }
      }
    }
  }

  // Adaptive

  // @include toDesktop {
  //   &__title-container {
  //      &__content-first {
  //       &__title {}

  //       &__text {}
  //      }

  //      &__content-second {
  //       &__title-mini {}

  //       &__video {}

  //       &__card {}
  //      }
  //   }

  //   &__content-container {
  //     &__circle {}

  //     &__photo {
  //       &__image {}
  //     }

  //     &__card-photo {}
  //   }
  // }

  @include toDesktop {
    &__title-container {
      &__content-first {
        margin-left: clm_px3(0, 60, $desk, $lap);
        margin-right: 50px;

        &__title {
          font-size: px-to-rem(60);
        }

        &__text {
          font-size: px-to-rem(25);
        }
      }

      &__content-second {
        &__title-mini {
        }

        &__video {
        }

        &__card {
        }
      }
    }

    &__content-container {
      margin: 0;
      right: clm_px3(-110, 0, $desk, $lap);

      &__circle {
        $size: clm_px(700, 800, $desk);

        @include size-important($size);
      }

      &__photo {
        $size: clm_px(400, 500, $desk);

        @include size($size);

        &__image {
          @include size-important($size);
        }
      }

      &__card-photo {
        width: 300px;
        border-radius: 30px;
        left: -220px;
        bottom: -72px;
      }
    }
  }

  @include toLaptop {
    &__title-container {
      width: 500px;

      &__content-first {
        &__title {
          font-size: clm_px3(40, 60, $lap, $tab);
        }

        &__text {
          font-size: px-to-rem(20);
        }
      }

      &__content-second {
        display: flex;
        flex-direction: column;

        &__title-mini {
        }

        &__video {
        }

        &__card {
        }
      }
    }

    &__content-container {
      &__circle {
      }

      &__photo {
        &__image {
        }
      }

      &__card-photo {
      }
    }
  }

  @include toTablet {
    height: max-content;
    flex-direction: column-reverse;
    margin: 0;

    &__title-container {
      position: relative;
      padding: 0;
      padding-top: 10px;
      gap: 40px;

      &__content-first {
        margin: 0;
        gap: 10px;

        &__title {
          font-size: px-to-rem(42);
        }

        &__text {
          font-size: px-to-rem(22);
        }
      }

      &__content-second {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;

        &__title-mini {
          padding-left: 10px;
          margin-bottom: -4px;
        }

        &__video {
        }
        &__card {
        }
      }
    }

    &__content-container {
      position: relative;
      margin: 0;
      margin-top: -50px;
      margin-bottom: -70px;
      top: -30px;
      right: -10%;
      left: inherit;
      bottom: inherit;

      &__circle {
        // @include size-important(300px);
        position: relative;
        right: -10%;
        place-self: end;
      }

      &__photo {
        // $size: 200px;

        // @include size($size);

        &__image {
          // @include size-important($size);
        }
      }

      &__card-photo {
        border-radius: 30px;
        left: -200px;
        bottom: -72px;

        & > * {
          @include size(100%);
          align-items: start;
          text-align: start !important;
        }

        & > * * {
          align-items: start;
          text-align: start !important;
        }

        & .card-block-4 {
          align-items: center;
        }

        & .card-block-4__container {
        }
      }
    }
  }

  @include toPhone {
    height: max-content;
    flex-direction: column-reverse;
    margin: 0;

    &__title-container {
      width: 100% !important;
      position: relative;
      padding: 0;
      padding-top: 10px;
      gap: 40px;

      &__content-first {
        width: 100%;
        margin: 0;
        gap: 10px;

        &__title {
          font-size: clm_px3(22, 30, $phone, $minWindowWidth);
        }

        &__text {
          font-size: px-to-rem(14);
        }
      }

      &__content-second {
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;

        &__title-mini {
          padding-left: 10px;
          margin-bottom: -4px;
        }

        &__video {
        }
        &__card {
        }
      }
    }

    &__content-container {
      position: relative;
      margin: 0;
      top: -30px;
      right: clm_px3(-50, 70, $phone, $minWindowWidth);
      left: inherit;
      bottom: inherit;

      &__circle {
        $size: clm_px3(300, 500, $phone, $minWindowWidth);

        @include size-important($size);
        position: relative;
        right: -10%;
        place-self: end;
      }

      &__photo {
        // $size: 200px;
        $size: clm_px3(200, 300, $phone, $minWindowWidth);

        @include size($size);

        &__image {
          @include size-important($size);
        }
      }

      &__card-photo {
        width: 188px;
        border-radius: 30px;
        left: -90px;
        bottom: -72px;

        & > * {
          @include size(100%);
          align-items: start;
          text-align: start !important;
        }

        & > * * {
          align-items: start;
          text-align: start !important;
        }

        & .card-block-4 {
          align-items: center;
        }

        & .card-block-4__container {
        }
      }
    }
  }
}
