.card-easy-8 {
  $origin: &;
  @include simple-property-DF(padding, 32);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-bottom: 16px;

    @include toTablet {
      gap: 12px;
    }
    
    @include toPhone {
      gap: 8px;
    }
  }

  &__image {
    @include shadow;
    @include min-size(78px);
    padding: 18px;
    border-radius: 40%;
    background-color: var(--primary-surface-white-hover);
    
    @include toTablet {
      @include min-size(58px);
      padding: 10px;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 22, 20, 18, 16, 16);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }
  
  &__text {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 16, 16, 14, 14, 12);
    line-height: 130%;
    color: var(--text-talee-light);
  }
}
