@mixin position-transform-symbol() {
  &- {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 0;
    transform: none;
  }

  &- {
    position: absolute;
    top: initial;
    right: initial;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &- {
    position: absolute;
    top: initial;
    right: 0;
    bottom: 0;
    left: initial;
    transform: none;
  }

  &- {
    position: absolute;
    top: 0;
    right: initial;
    bottom: initial;
    left: 0;
    transform: none;
  }

  &- {
    position: absolute;
    top: 0;
    right: initial;
    bottom: initial;
    left: 50%;
    transform: translate(-50%, 0);
  }

  &- {
    position: absolute;
    top: 0;
    right: 0;
    bottom: initial;
    left: initial;
    transform: none;
  }

  &- {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 0;
    transform: translate(0, -50%);
  }

  &- {
    position: absolute;
    top: 50%;
    right: 0;
    bottom: initial;
    left: initial;
    transform: translate(0, -50%);
  }

  &-c {
    position: absolute;
    top: 50%;
    right: initial;
    bottom: initial;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &-n {
    position: absolute;
    top: initial;
    right: initial;
    bottom: initial;
    left: initial;
    transform: none;
  }
}

.screen {
  @include fullscreen;

  &--no-event {
    @include no-event;
  }

  &__container {
    @include size(100%);
    position: relative;

    & > * {
      position: absolute;
    }
  }

  &-desk {
    @include position-transform-symbol;
  }

  &-lab {
    @include toDesktop {
      @include position-transform-symbol;
    }
  }

  &-tab {
    @include toLaptop {
      @include position-transform-symbol;
    }
  }

  &-phone {
    @include toPhone {
      @include position-transform-symbol;
    }
  }
}
