.card-image-2 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  position: relative;

  height: 100%;

  overflow: hidden;

  border-radius: 20px;
  box-shadow: 0 3px 4px 0 rgb(31 33 29 / 10%);
  padding: 30px;

  &__image {
    display: block;
    position: absolute;
    inset: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  $blur: 3px;

  &__container {
    position: relative;
    width: 100%;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 24, 24, 22, 20, 18);
    line-height: 130%;
    text-transform: uppercase;
    color: #fffefd;
  }
}
