.title-easy {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;
  gap: 8px;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(48);

    @include simple-property-DF(font-size, 48, 48, 36, 30, 24);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);

    @include simple-property-DF(font-size, 18, 18, 16, 16, 14);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  &__container {
  }
}
