.modal {
  overflow: hidden;
  position: relative;
  margin: auto;

  border-radius: 30px;
  padding: 64px;
  width: 100%;
  max-width: 1234px;

  box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);
  background: var(--primary-surface-white);

  &__button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 30px;
    z-index: 10;
  }

  &__content {
    position: relative;
    z-index: 1;
  }

  &__footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    width: 100%;
    margin: auto;
    margin-top: 80px;
  }

  @include toLaptop {
    padding: 40px;
  }

  @include toTablet {
    padding: 40px;

    &__button {
      margin: 20px;
    }
  }

  @include toPhone {
    padding: 20px 10px;

    &__button {
      margin: 10px;
    }

    &__footer {
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-top: 20px;
    }
  }
}
