// @use "./breakpoints" as *;

@mixin toDesktop {
  @media (max-width: #{$desk + px} ) {
    @content;
  }
}

@mixin toLaptop {
  @media (max-width: #{$lap + px}) {
    @content;
  }
}

@mixin toTablet {
  @media (max-width: #{$tab + px}) {
    @content;
  }
}

@mixin toPhablet {
  @media (max-width: #{$phab + px}) {
    @content;
  }
}

@mixin toPhone {
  @media (max-width: #{$phone + px}) {
    @content;
  }
}

@mixin toMedia($size) {
  @media (max-width: #{$size + px}) {
    @content;
  }
}

@mixin onMedia($size) {
  @media (min-width: #{$size + px}) {
    @content;
  }
}
