/* Imports */
@use "sass:math";

/* Mixins */

@mixin setAbsoluteColor($color) {
  /* (love hate) LVHA (link visited hover active) */
  color: $color;

  &:link {
    color: $color;
  }

  &:visited {
    color: $color;
  }

  &:hover {
    /* text-decoration: underline */
  }

  &:active {
    color: $color;
  }
}

@mixin hover($transition-time: var(--transition-time)) {
  transition: all $transition-time;

  @media (hover: hover) {
    &:hover {
      @content;
    }
  }

  @media (hover: none) {
    &:active {
      @content;
    }
  }
}

@mixin shadow {
  box-shadow: 0 3px 10px 0 rgb(31 33 29 / 7%);
}

@mixin fullscreen {
  position: absolute;
  inset: 0;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin grid-center {
  display: grid;
  place-items: center;
}

@mixin size($_size) {
  width: #{$_size};
  height: #{$_size};
}

@mixin size-important($_size) {
  width: #{$_size} !important;
  height: #{$_size} !important;
}

@mixin min-size($_size) {
  min-width: #{$_size};
  min-height: #{$_size};
}

@mixin max-size($_size) {
  max-width: #{$_size};
  max-height: #{$_size};
}

@mixin no-event {
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
}

// Effects
@mixin addMaskCircle($-circle-size, $-dirrection: "right") {
  @if $-dirrection == "right" {
    clip-path: circle(
      var($-circle-size) at calc((var($-circle-size) - 100%) * -1) 50%
    );
  } @else if $-dirrection == "left" {
    clip-path: circle(var($-circle-size) at var($-circle-size) 50%);
  } @else if $-dirrection == "top" {
    clip-path: circle(var($-circle-size) at 50% var($-circle-size));
  } @else if $-dirrection == "botton" {
    clip-path: circle(
      var($-circle-size) at 50% calc((var($-circle-size) - 100%) * -1)
    );
  }
}

// Fonts

@mixin text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@mixin text-ellipsis-lines($lines: 2) {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $lines;
  line-clamp: $lines;
}

/* -------------------------------------------------- */

@mixin object-center {
  object-fit: cover;
  object-position: center;
}

/* -------------------------------------------------- */

@mixin overflow-clip {
  @supports (overflow: clip) {
    overflow: clip;
  }

  @supports not (overflow: clip) {
    overflow-x: hidden;
  }
}

@mixin fluid-grid($min-size: 200px) {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax($min-size, 1fr));
}

