/* -------------------------------------------------- */

//      rem
@function clm_rem($min, $max, $width) {
  $min_rem: #{$min + rem};
  $max_rem: #{$max + rem};
  $width_vw: #{calc(round(($max * 16) / ($width / 100) * 100000) / 100000) + vw};

  @return clamp($min_rem, $width_vw, $max_rem);
}

//   ,    2rem,
//  1.5rem    ,
//     600px

// font-size: clm_rem(1.5, 2, 600);

//    px
@function clm_px($min, $max, $width: $maxWindowWidth) {
  $min_px: #{$min + px};
  $max_px: #{$max + px};
  $width_vw: #{calc(round($max / ($width / 100) * 100000) / 100000) + vw};

  @return clamp($min_px, $width_vw, $max_px);
}

//       .
//   30px,  20px.
//       600px

// padding: clm_px(20, 30, 600);

/*  */

@mixin padding-adaptive($min, $max, $width) {
  padding: clm_px($min, $max, $width);
}

@mixin margin-adaptive($min, $max, $width) {
  margin: clm_px($min, $max, $width);
}

/*  */

@function clm_px2($minVal, $maxVal, $maxWidth: $maxWindowWidth, $minWidth: 0) {
  $normalizedWidth: math.div(-$minWidth, ($maxWidth - $minWidth));
  $addVal: $normalizedWidth * ($maxVal - $minVal);
  $expectMinVal: $minVal + $addVal;
  $width_vw: #{calc(round($maxVal / ($maxWidth / 100) * 100000) / 100000) + vw};

  @return clamp(#{$expectMinVal + px}, $width_vw, #{$maxVal + px});
}

/*  */

@function clm_px3(
  $minVal,
  $maxVal,
  $maxWidth: $maxWindowWidth,
  $minWidth: 0,
  $var: "px"
) {
  @return max(
    #{$minVal + $var},
    min(
      #{$maxVal + $var},
      calc(
        #{$minVal + $var} + #{$maxVal - $minVal} *
          ((100vw - #{$minWidth + px}) / #{$maxWidth - $minWidth})
      )
    )
  );
}

@function clm_px4(
  $minVal,
  $maxVal,
  $maxWidth: $maxWindowWidth,
  $minWidth: 0,
  $reverse: false
) {
  @if $reverse {
    @return max(
      #{$minVal + px},
      min(
        #{$maxVal + px},
        calc(
          #{$minVal + px} - (#{$maxVal - $minVal} * ((100vw - #{$maxWidth + px}) /
                    #{$maxWidth - $minWidth}))
        )
      )
    );
  } @else {
    @return max(
      #{$minVal + px},
      min(
        #{$maxVal + px},
        calc(
          #{$minVal + px} + (#{$maxVal - $minVal} * ((100vw - #{$minWidth + px}) /
                    #{$maxWidth - $minWidth}))
        )
      )
    );
  }
}

/*  */

@mixin property-value($property, $value) {
  #{$property}: $value !important;
}

@function adapter($minVal, $maxVal, $maxWidth, $minWidth: 0) {
  $reverse: $minVal > $maxVal;

  @return clm_px4($minVal, $maxVal, $maxWidth, $minWidth, $reverse);
}

/*  */

$percent: 0.75;

@mixin fluid-property-DF(
  $property,
  $max_,
  $desk_: $max_ * $percent,
  $lap_: $desk_ * $percent,
  $tab_: $lap_ * $percent,
  $phone_: $tab_ * $percent,
  $min_: $phone_ * $percent
) {
  @include property-value(
    $property,
    adapter($desk_, $max_, $maxWindowWidth, $desk)
  );

  @include toDesktop {
    @include property-value($property, adapter($lap_, $desk_, $desk, $lap));
  }

  @include toLaptop {
    @include property-value($property, adapter($tab_, $lap_, $lap, $tab));
  }

  @include toTablet {
    @include property-value($property, adapter($phone_, $tab_, $tab, $phone));
  }

  @include toPhone {
    @include property-value(
      $property,
      adapter($min_, $phone_, $phone, $minWindowWidth)
    );
  }
}

@mixin fluid-property-MF(
  $property,
  $min_,
  $phone_: $min_ * ($percent + 1),
  $tab_: $phone_ * ($percent + 1),
  $lap_: $tab_ * ($percent + 1),
  $desk_: $lap_ * ($percent + 1),
  $max_: $desk_ * ($percent + 1)
) {
  @include property-value(
    $property,
    adapter($min_, $phone_, $maxWindowWidth, $desk)
  );

  @include onPhone {
    @include property-value($property, adapter($phone_, $tab_, $phone, $minWindowWidth));
  }

  @include onTablet {
    @include property-value($property, adapter($tab_, $lap_, $tab, $phone));
  }

  @include onLaptop {
    @include property-value($property, adapter($lap_, $desk_, $lap, $tab));
  }

  @include onDesktop {
    @include property-value($property, adapter($desk_, $max_, $desk, $lap));
  }
}

/* -------------------------------------------------- */

//     ( 16px)
$base-font-size: 16;

//    px  rem
// font-size: px-to-rem(18);
@function px-to-rem($px-value) {
  @return #{math.div($px-value, $base-font-size)}rem;
}

/*  */

@mixin simple-property-DF(
  $property,
  $size-max,
  $size-desktop: $size-max * $percent,
  $size-laptop: $size-desktop * $percent,
  $size-tablet: $size-laptop * $percent,
  $size-phone: $size-tablet * $percent
) {
  @include property-value($property, px-to-rem($size-max));

  @include toDesktop {
    @include property-value($property, px-to-rem($size-desktop));
  }

  @include toLaptop {
    @include property-value($property, px-to-rem($size-laptop));
  }

  @include toTablet {
    @include property-value($property, px-to-rem($size-tablet));
  }

  @include toPhone {
    @include property-value($property, px-to-rem($size-phone));
  }
}

/* -------------------------------------------------- */

@mixin size-scrollbar($size) {
  &::-webkit-scrollbar {
    @include size($size);
  }
}

/* -------------------------------------------------- */

@mixin container($min-width: null, $max-width: null) {
  @if $min-width != null and $max-width != null {
    @container (min-width: #{$min-width}) and (max-width: #{$max-width}) {
      @content;
    }
  } @else if $min-width != null {
    @container (min-width: #{$min-width}) {
      @content;
    }
  } @else if $max-width != null {
    @container (max-width: #{$max-width}) {
      @content;
    }
  }
}

