.card-easy-5 {
  $origin: &;

  padding: 24px;
  display: flex;
  gap: 20px;

  &__image {
    @include size(100px);
    @include min-size(100px);
    object-fit: contain;
    filter: drop-shadow(0 0 14px rgb(180 174 167 / 19%));
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);
    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);
    margin-bottom: 16px;
  }

  &__block {
    & p {
      font-size: px-to-rem(24);
      line-height: 130%;
      color: var(--text-talee-dark);
    }

    & b {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(36);
      line-height: 110%;
      color: var(--text-talee-accent);
    }
  }

  @include toPhone {
    padding: 12px 6px;
    gap: 12px;

    &__image {
      @include size(70px);
      @include min-size(70px);
    }

    &__title {
      font-size: px-to-rem(14);
      margin-bottom: 6px;
    }
    
    &__text {
      font-size: px-to-rem(12);
      margin-bottom: 6px;
    }

    &__block {
      & b {
        font-size: px-to-rem(30);
      }

      & p {
        font-size: px-to-rem(18);
      }
    }
  }
}
