.input-new {
  position: relative;
  display: flex;
  width: 100%;
  justify-content: start;
  align-items: center;

  &__input {
    display: block;
    width: 100%;
    padding: 26px 45px;
    background: transparent;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);

    &:focus {
      outline: none;
    }

    &::placeholder {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(18);
      line-height: 130%;
      color: var(--text-talee-dark);
      opacity: 0.5;
    }
  }

  &--search {
    &::before {
      @include size(22px);
      content: "";
      display: inline-block;
      cursor: pointer;
      background-image: url("../images/icons/search_.svg");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      transform: translate(26px, -2px);

      transition: all var(--transition-time);
    }

    @include hover {
      &::before {
        transform: translate(26px, -2px) scale(1.2);
      }
    }
  }

  @include toPhone {
    &__input {
      padding: 15px 20px;
      font-size: px-to-rem(16);

      &::placeholder {
        font-size: px-to-rem(16);
      }
    }

    &--search {
      &::before {
        transform: translate(12px, 0px);
      }

      @include hover {
        &::before {
          transform: translate(12px, 0px) scale(1.2);
        }
      }
    }
  }
}

.textarea {
  resize: none;
  display: block;
  width: 100%;
  padding: 0 45px;
  margin: 26px 0;
  background: transparent;

  &:focus {
    outline: none;
  }

  &::placeholder {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-dark);
    opacity: 0.5;
  }

  @include toPhone {
    padding: 0 20px;
    margin: 15px 0;
  }
}


