.button-accordion-icon {
  $origin: &;

  @include flex-center;

  flex-direction: row;
  position: relative;
  z-index: 1;

  &__container {
    @include flex-center;
    @include size(56px);
    border: 1px solid var(--primary);
    border-radius: 100px !important;
    box-shadow: 0 3px 4px 0 rgb(31 33 29 / 11%);
    background: var(--text-talee-white);
    cursor: pointer;
    transition: all var(--transition-time);

    &--active {
      transform: scale(1.2) !important;
      box-shadow: 0 0 25px -5px var(--accent) !important;

      & > #{$origin}__image {
        //
      }
    }

    @include hover {
      transform: scale(1.1);
      box-shadow: 0 5px 10px 0 rgb(31 33 29 / 11%);
    }
  }

  &__pop-up {
    width: max-content;
    position: fixed;
    right: calc(100% + 20px) !important;
    transform-origin: center right !important;

    display: flex;
    gap: 20px;

    transition: all var(--transition-time);
    z-index: 2;

    &--hidden {
      @include no-event;
      opacity: 0;
      transform: scale(0);
    }
  }

  &__menu {
    height: 82dvh;
    max-height: 920px;
    max-width: 764px;
    
    @include toLaptop {
      max-width: 75vw;
      max-height: 720px;
    }
    
    @include toTablet {
      max-height: 580px;
    }
    
    @include toPhone {
      height: 88dvh;
      max-width: 100vw;
    }
  }

  // Adaptive

  @include toPhone {
    &--modal-menu {
      #{$origin}__pop-up {
        z-index: 100;
        right: calc(100% - 48px) !important;
      }
    }

    #{$origin}__image {
      @include size(70%);
    }

    #{$origin}__container {
      @include size(36px);
    }
  }

  // Effect

  &--col {
    #{$origin}__pop-up {
      flex-direction: column;
    }
  }
}
