.card-info-doc {
  display: flex;
  flex-direction: column;
  gap: 20px;

  &__card {
    margin: 32px;
  }

  &__container {
    display: grid;
    grid-template-columns: 2fr 40px 1fr;
  }

  &__line {
    width: 1px;
    height: 100%;
    margin: auto;

    background: var(--text-tale-title);
    opacity: 0.15;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  & p {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    color: var(--text-talee-light);

    margin-bottom: 20px;
  }

  @include toPhone {
    gap: 10px;

    &__container {
      display: grid;
      grid-template-columns: 2fr 20px 1fr;

      overflow-x: scroll;
      border: 1px solid var(--primary-50p);
      border-radius: 20px;
      padding: 10px;
    }

    &__title {
      font-size: px-to-rem(20);
    }
    
    &__text {
      font-size: px-to-rem(16);
    }

    &__card {
      margin: 10px;
    }

    & p {
      margin-bottom: 10px;
    }
  }
}
