.card-block-6 {
  $origin: &;

  --padding: 32px;

  position: relative;
  padding: var(--padding);

  &::after {
    content: attr(data-content);
    display: block;
    position: absolute;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(20);
    line-height: 120%;
    color: var(--primary-surface-white);
    z-index: 2;

    // top: calc(var(--padding) + 258px);
    left: calc(var(--padding) + 20px + 30px);

    @include simple-property-DF(top, 258, 240, 220, 180, 170);
  }

  &::before {
    content: "";
    display: block;
    position: absolute;

    @include size(24px);
    background-image: url("../images/icons/calendar.svg");
    background-size: cover;

    // top: calc(var(--padding) + 256px);
    left: calc(var(--padding) + 20px);

    @include simple-property-DF(top, 258, 240, 220, 180, 170);
  }

  &__image {
    border-radius: 20px;
    overflow: hidden;
    width: 100%;

    @include simple-property-DF(height, 298, 280, 260, 220, 200);
    object-fit: cover;

    margin-bottom: 20px;
  }

  &__title {
    @include simple-property-DF(font-size, 24, 22, 20, 18, 20);

    font-family: var(--font-family);
    font-weight: 400;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-tale-title);
    
    margin-bottom: 12px;
  }

  &__text {
    @include text-ellipsis-lines;
    @include simple-property-DF(font-size, 22, 20, 18, 16, 14);

    font-family: var(--second-family);
    font-weight: 400;
    line-height: 120%;
    color: var(--text-talee-light);


    margin-bottom: 20px;
  }

  &--normal {
    padding: 0;

    &::after {
      top: 32px + 220;
      left: 20px + 30;
    }

    &::before {
      top: 32px + 220;
      left: 20px;
    }
  }

  @include toPhone {
    --padding: 8px;

    &::after {
      font-size: px-to-rem(16);
    }

    &::before {
      @include size(20px);
    }

    &__image {
      margin-bottom: 10px;
    }

    // &__title {
    //   font-size: px-to-rem(20);
    // }

    &__text {
      font-size: px-to-rem(14);
    }
  }
}
