@import "libs/imports";
@import "layouts/base-layout";

/* + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + + */

/* Decors */

.decor-circle-border {
  width: 764px;
  height: 744px;
  position: absolute;
  z-index: 0;
  border: 2px solid var(--light-blue);
  border-radius: 100%;
  top: -162px;

  @include toDesktop {
    @include size(554px);
    top: -126px;
  }

  @include toPhablet {
    @include size(687px);
    top: -160px;
  }

  @include toPhone {
    @include size(384px);
  }
}

.decor-shadow-gradient {
  position: relative;

  & * {
    z-index: 1;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 0;

    filter: blur(150px);
    background: linear-gradient(
      360deg,
      rgb(150 182 220 / 23%) 0%,
      rgb(166 239 221 / 23%) 100%
    );
  }
}

/* Images Components */

.img-decor {
  @include size(294px);

  border-radius: 100%;
  overflow: hidden;
  background-image: url("../images/decor/decore_2.jpg");
  mix-blend-mode: overlay;
  background-size: cover;

  @include toDesktop {
    @include size(122px);

    transform: translateY(40px);
    margin-right: 74px;
  }

  @include toLaptop {
    @include size(194px);

    margin: auto;
  }
}

/* Other */

.block-circle {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: relative;

  @include size(834px);

  padding: 42px;
  border-radius: 100%;
  background-color: var(--white);

  &__image {
    margin-bottom: 60px;
  }

  &__image-2 {
    @include toPhone {
      height: 90px;
      align-self: flex-end;
      margin-top: -30px;
    }
  }

  &__container {
    @include flex-center;
    flex-direction: column;
    text-align: center;
  }

  &__text {
    font-size: px-to-rem(19);
    line-height: 87%;
    letter-spacing: 0.02em;
  }

  &__title {
    font-family: var(--second-family);
    font-size: px-to-rem(45);
    line-height: 100%;
    letter-spacing: 0.02em;
    color: var(--dark-blue);
  }

  @include toLaptop {
    @include size(712px);

    &__title {
      font-size: px-to-rem(36);
    }

    &__image {
      height: 50px;
    }
  }

  @include toPhone {
    position: relative;
    margin-top: 200px;
    width: 100%;
    height: max-content;
    border-radius: 30px;
    padding: 0 15px;

    &::after {
      @include size(500px);
      content: "";
      display: block;
      background: var(--white);
      position: absolute;
      top: 0%;
      transform: translateY(-20%);
      z-index: -1;
      border-radius: 100%;
    }

    &__image {
      margin-bottom: 14px;
      height: 70px;
    }

    &__decor {
      margin-bottom: 38px;
    }

    &__text {
      font-size: px-to-rem(16);
      margin-bottom: 12px;
    }

    &__title {
      font-size: px-to-rem(20);
    }
  }
}

/* New */

.decor-circle-info {
  @include size(200px);
  @include flex-center;

  background-color: var(--primary-surface-white);
  border: 2px solid var(--primary-material-100);
  border-radius: 100%;

  &--blue {
    border: 2px solid var(--second-color-light-active);
  }

  &__container {
    @include flex-center;
    flex-direction: column;
    transform: rotate(-30deg);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 900;
    font-size: px-to-rem(20);
    line-height: 130%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--neutral-color-30);
  }

  @include toDesktop {
    @include size(170px);

    &__image {
      width: 80px;
    }

    &__text {
      font-size: px-to-rem(16);
    }
  }

  @include toLaptop {
    @include size(126px);

    &__image {
      width: 60px;
    }

    &__text {
      font-size: px-to-rem(12);
    }
  }

  @include toTablet {
    @include size(75px);

    &__image {
      width: 34px;
    }

    &__text {
      font-size: px-to-rem(7);
    }
  }

  // @include toPhone {
  //   @include size(55px);
  // }
}

// ---

.section-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;

  margin-top: -30px;

  @include toTablet {
    margin-top: -12px;
    grid-template-columns: 1fr;
    gap: 8px;
  }

  &--no-margin {
    margin: 0;
  }
}

.section-0 {
  z-index: 1;
  border-radius: 40px;
  padding: 40px;
  box-shadow: 0 0 13px 0 rgb(180 174 167 / 22%);
  background: #fffffa;

  display: grid;
  grid-template-columns: 670px 1fr;
  gap: 20px;

  &__main-photo {
    @include size(100%);
    object-fit: cover;
    border-radius: 30px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(28);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 40px;
  }

  &__second-title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(24);
    line-height: 130%;
    text-transform: uppercase;
    color: #5d5955;

    padding-left: 20px;
    margin-bottom: 15px;
  }

  &__card {
    padding: 14px 32px;
  }

  &__container {
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 20px;
  }

  &__block-media {
    overflow: hidden;
    place-self: end center;
    border-radius: 20px;
    width: 318px;
    height: 216px;
    background: radial-gradient(
      60.21% 60.21% at 48.46% 50%,
      rgb(70 67 67) 0%,
      rgb(98 80 15) 100%
    );

    @include flex-center;
  }

  &__text {
    @include flex-center;

    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-transform: uppercase;
    text-align: justify;
    color: var(--dark-gray);

    & span {
      // font-weight: 400;
      font-size: px-to-rem(18);
      color: var(--text-talee-accent);
    }
  }

  /* Adaptive */
  $origin: &;

  @include toDesktop {
    padding: 22px;

    #{$origin}__block-media {
      width: 240px;
      height: 162px;
    }

    #{$origin}__text {
      font-size: px-to-rem(16);

      & span {
        font-size: px-to-rem(16);
      }
    }
  }

  @include toLaptop {
    padding: 12px;
  }

  @include toTablet {
    display: flex;
    flex-direction: column-reverse;

    #{$origin}__main-photo {
      border-radius: 25px;
      height: 265px;
    }

    #{$origin}__title {
      margin: 0;
      height: max-content;
    }
  }

  @include toPhablet {
    #{$origin}__block-media {
      width: 100%;
      height: 216px;

      & > img {
        @include size(100%);
        object-fit: cover;
      }
    }

    #{$origin}__container {
      grid-template-columns: 1fr;
    }
  }
}

.form-block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;

  @include toTablet {
    display: flex;
    flex-direction: column;
  }
}

