.circle-block {
  $origin: &;

  @include flex-center;
  flex-direction: column;

  width: 100%;
  max-width: 450px;
  aspect-ratio: 1/1;
  overflow: hidden;

  border-radius: 100%;
  background: var(--primary-surface-white);
  border: 2px solid rgb(234 133 41 / 25%);
  box-shadow: 0 0 14px 0 rgb(180 174 167 / 19%);

  font-family: var(--font-family);
  font-weight: 400;
  line-height: 100%;
  color: var(--text-tale-title);

  font-size: clm_px(10, 32, 1920);

  @include toDesktop {
    font-size: clm_px(20, 32, $desk);
  }

  & img {
    margin-top: 5px;
  }

  & img[alt="time"] {
    margin-top: 2px;
  }

  &__wrapper {
    @include flex-center;
    gap: 18px;
    flex-direction: column;
    align-items: start;
  }

  &__container {
    @include flex-center;
    position: relative;
    align-items: start;
    gap: 10px;
  }

  &__time {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 120%;
    color: var(--text-talee-dark);

    & i {
      font-weight: 300;
    }
  }

  &__image {
    &-1 {
      transform: translateY(120px);
    }
  }

  // Effects

  &--mini {
    @include size(100%);
    @include max-size(236px);

    #{$origin}__container {
      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(17);
      line-height: 130%;
      color: var(--text-talee-title);

      & > img {
        @include min-size(13px);
        @include size(13px);
      }
    }

    #{$origin}__time {
      font-size: px-to-rem(14);
    }
  }

  &--micro {
    @include size(100%);
    @include max-size(195px);

    #{$origin}__container {
      font-family: var(--second-family);
      font-weight: 500;
      font-size: px-to-rem(16);
      line-height: 130%;
      color: var(--text-talee-title);

      gap: 4px;

      & > img {
        @include min-size(13px);
        @include size(13px);
      }
    }

    #{$origin}__time {
      font-size: px-to-rem(14);
    }
  }

  &--blue {
    border: 2px solid rgb(41 92 234 / 25%);
  }
}

.section-contacts {
  @include fluid-grid(320px);
  place-content: center;
  place-items: center;
  width: 100%;
  gap: 20px;

  @include toPhone {
    @include fluid-grid(290px);
  }
}

.shlem-head {
  $origin: &;
  perspective: 700px;

  width: 100%;
  max-width: 450px;
  aspect-ratio: 1;

  // @include overflow-clip;
  // overflow: hidden;

  &__wrapper {
    @include flex-center;
    position: relative;
  }

  &__face {
    @include size(100%);
    @include flex-center;
    position: absolute;
  }

  &__eyes {
    position: absolute;
    top: 58%;
  }

  &__nose {
    position: absolute;
    top: 68%;
    transition: all var(--transition-time);

    @include hover {
      transform: scale(1.2);
    }
  }

  &__mouth {
    position: absolute;
    top: 75%;

    transition: all var(--transition-time);

    @include hover {
      transform: scale(1.05);
    }
  }

  &--mini {
    max-width: 230px;

    #{$origin}__eyes {
      transform: scale(0.8);
    }

    #{$origin}__nose {
      @include size(16px);
    }
    #{$origin}__mouth {
      @include size(50%);
    }
  }
}

.eyes {
  display: flex;
  justify-content: center;

  gap: 46px;

  &__wrapper {
    position: relative;
  }

  &__eye {
    @include min-size(30px);
    @include size(30px);
    position: relative;
    background-color: #62500f;
    border-radius: 100%;
    z-index: 3;
  }

  &__rot {
    @include flex-center;
    @include size(100%);
    position: relative;
  }

  &__pupil {
    position: absolute;

    @include min-size(12px);
    @include size(12px);
    left: 5%;
    background-color: #fff;
    border-radius: 100%;
  }

  &__blick-1 {
    @include min-size(7px);
    @include size(7px);
    @include max-size(7px);
    position: absolute;
    background-color: rgb(255 255 255 / 75%);
    border-radius: 100%;
    bottom: 25%;
    left: 10%;

    &--right {
      left: auto;
      right: 10%;
    }
  }

  &__blick-2 {
    @include min-size(4px);
    @include size(4px);
    @include max-size(4px);
    position: absolute;
    background-color: rgb(255 255 255 / 75%);
    border-radius: 100%;
    bottom: 10%;
    left: 30%;

    &--right {
      left: auto;
      right: 30%;
    }
  }

  &__shadow {
    position: absolute;
    left: -4px;
    bottom: -2px;
    width: 14px;
    height: 8px;
    opacity: 0.5;
    border-radius: 100%;
    background: var(--primary);

    &--right {
      left: auto;
      right: -4px;
    }
  }

  &__brow {
    position: absolute;
    top: -16px;
    left: 20%;

    &--right {
      left: auto;
      right: 20%;
      transform: scaleX(-1);
    }
  }
}

