      left: 38%;

      @include toTablet {
        top: 36%;
        left: 30%;
      }

      @include toPhone {
        left: initial;
        top: initial;
        bottom: 0%;
        right: 0%;
      }
    }

    &--2 {
      position: absolute;
      top: 58%;
      right: 25%;

      @include toDesktop {
        right: 45%;
      }

      @include toLaptop {
        right: 25%;
      }

      @include toTablet {
        right: 45%;
      }

      @include toPhone {
        right: initial;
        left: 0%;
        display: none;
      }
    }

    &--3 {
      position: absolute;
      bottom: 27%;
      left: 22%;

      @include toTablet {
        left: 18%;
