.pop-up-repeat {
  &__title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(36);
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: var(--primary);
    margin-bottom: 30px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
    justify-content: center;
    align-items: center;
    place-items: center;
    margin-top: 40px;
  }

  @include toLaptop {
    &__title {
      font-size: px-to-rem(28);
    }

    &__container {
      grid-template-columns: repeat(4, 1fr);
      gap: 5px;
    }

    &__list {
      grid-row: 1 / 1;
      grid-column: 1 / 5;
    }
  }

  @include toTablet {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }

    &__list {
      grid-row: 1 / 1;
      grid-column: 1 / 3;
    }
  }

  @include toPhone {
    &__container {
      display: flex;
      flex-direction: column;
      gap: 10px;

      & > * {
        place-self: start;
      }

      & > *:nth-child(2n) {
        place-self: end;
      }
    }

    &__list {
      order: -1;
      place-self: center;
    }
  }
}
