// /* Page */

.page {
  $origin: &;
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
  height: 100%;

  color: var(--text);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: px-to-rem(16);
  line-height: 110%;

  &__body {
    width: 100%;
    height: 100%;
    margin: 0 auto;

    position: relative;
    background-color: var(--bg);
    transition: color var(--transition-time),
      background-color var(--transition-time);

    @include toLaptop {
      transition: all var(--transition-time);
    }

    &--stop-scroll {
      overflow: hidden;

      padding-right: var(--scroll-bar-size);
    }
  }

  &__wrapper {
    width: 100%;
    max-width: 1920px;
    min-height: 100dvh;
    margin: auto;
    display: flex;
    flex-direction: column;
  }

  &__header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;

    z-index: 100;
  }

  &__menu {
    @include no-event;

    position: sticky;
    bottom: 0;

    display: flex;
    justify-content: end;

    overflow: hidden;
    z-index: 10;

    height: 100dvh;
    margin-top: -100dvh;

    padding-right: 30px;
    padding-bottom: 30px;

    @include toDesktop {
      padding-right: 20px;
      padding-bottom: 20px;
    }

    @include toLaptop {
      padding-right: 16px;
      padding-bottom: 16px;
    }

    @include toTablet {
      padding-right: 10px;
      padding-bottom: 10px;
    }

    /* ------------------------------- */

    &__container {
      @include flex-center;
      align-items: end;
      
      perspective: 200px;
      flex-direction: column;
      gap: var(--menu-gap);
      place-self: flex-end;
      pointer-events: all;
      
      &__up {
        @include flex-center;
        z-index: 1;
        flex-direction: column;
        width: 68px;
        gap: var(--menu-gap);

        @include toPhone {
          width: 40px;
        }
      }

      &__contacts {
        position: relative;
        z-index: 2;
      }
    }
  }

  &__main {
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    gap: 60px;

    min-height: 100%;

    @include overflow-clip;
    position: relative;

    &::before {
      //       .
      @include fullscreen;
      @include no-event;
      content: "";
      box-shadow: inset 0 0 42px 8px var(--bg);
      z-index: 1;
    }

    padding: 0 30px;
    margin-bottom: 60px;

    @include toDesktop {
      padding: 0 20px;
      gap: 40px;
    }

    @include toPhone {
      padding: 0 15px;
      gap: 20px;
    }
  }

  &__footer {
    // padding: 0 30px;
    // margin: 0 -30px;
    
    // @include toDesktop {
    //   padding: 0 20px;
    //   margin: 0 -20px;
    // }
    
    // @include toPhone {
    //   padding: 0 15px;
    //   margin: 0 -15px;
    // }
  }

  &--blue {
    #{$origin}__body {
      background-color: #F9FCFE;
    }

    #{$origin}__main {
      &::before {
        box-shadow: inset 0 0 42px 8px #F9FCFE;
      }
    }
  }
}
