.button {
  $origin: &;

  @include toDesktop {
    min-height: 46px;

    #{$origin}__text {
      font-size: px-to-rem(17);
    }

    #{$origin}__image {
    }

    #{$origin}__icon {
      & svg {
        @include size(13px);
      }
    }
  }

  @include toLaptop {
    min-height: 44px;
    padding: 0 22px;

    #{$origin}__text {
      font-size: px-to-rem(16);
    }

    #{$origin}__image {
    }

    #{$origin}__icon {
      & svg {
        @include size(12px);
      }
    }
  }

  @include toTablet {
    min-height: 42px;

    #{$origin}__text {
      font-size: px-to-rem(15);
    }

    #{$origin}__image {
    }

    #{$origin}__icon {
      & svg {
        @include size(11px);
      }
    }
  }

  @include toTablet {
    padding: 0 18px;
  }

  @include toPhone {
    min-height: 32px;
    padding: 0 16px;
    gap: 6px;

    #{$origin}__text {
      font-size: px-to-rem(10);
    }

    #{$origin}__image {
    }

    #{$origin}__icon {
      & svg {
        @include size(8px);
      }
    }
  }
}
