.menu-object {
  display: grid;
  position: relative;
  width: 100%;
  height: 100%;
  grid-template-rows: 1fr max-content;

  &__header {
    @include flex-center;
    flex-direction: column;
    position: relative;
  }

  /* ------------------------------------- */

  &__object {
    width: 80%;
    height: 600px;

    &__image {}
  }

  

  @include toTablet {
    &__circle {
      width: 400px !important;
      height: 400px !important;
    }

    &__object {
      margin: auto;
    }
  }

  @include toPhone {
    &__circle {
      width: 300px !important;
      height: 300px !important;
    }

    &__object {
      margin: auto;
    }
  }

  /* ------------------------------------- */

  &__item-1 {
    pointer-events: all;
    position: absolute;
    top: 28%;
    left: 38%;
    z-index: 2;
  }

  &__item-2 {
    pointer-events: all;
    position: absolute;
    top: 44%;
    left: 50%;
    z-index: 2;
  }

  &__item-3 {
    pointer-events: all;
    position: absolute;
    top: 70%;
    left: 60%;
    z-index: 2;
  }

  // Blue

  &__item-4 {
    pointer-events: all;
    position: absolute;
    top: 31%;
    left: 30%;
    z-index: 2;
  }

  &__item-5 {
    pointer-events: all;
    position: absolute;
    top: 58%;
    left: 28%;
    z-index: 2;
  }

  &__item-6 {
    pointer-events: all;
    position: absolute;
    top: 60%;
    left: 73%;
    z-index: 2;
  }

  /* ------------------------------------- */

  &__photo {
    position: absolute;
    top: 0;
    bottom: 0;

    @include flex-center;
    transform: translateY(-20px);
  }

  &__footer {
    position: relative;
    bottom: 0;
    padding-inline: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    height: max-content;
  }

  &__comment {
    display: flex;
    flex-direction: column;
    padding: 20px;
    gap: 8px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;

    & b {
      font-weight: 400;
      text-transform: uppercase;
      color: var(--primary);
    }
  }

  // Effects

  $origin: &;

  &--blue {
    #{$origin}__title {
    }

    #{$origin}__comment b {
      color: var(--primary-blue);
    }
  }

  // Adaptive

  @include toDesktop {
    &__footer {
      overflow-y: auto;
      height: 200px;
      z-index: 2;
      pointer-events: all;
    }
  }

  @include toTablet {
    &__item-1,
    &__item-2-,
    &__item-3,
    &__item-4,
    &__item-5,
    &__item-6- {
      display: none;
    }

    &__item-6 {
      top: 55%;
      left: 70%;
    }

    &__item-2 {
      top: 50%;
      left: 70%;
    }
  }


  @include toPhone {
    &__footer {
      display: flex;
      flex-direction: column;
      gap: 10px;
      padding-inline: 10px;
      height: 140px;
    }

    &__comment {
      padding: 10px;
      font-size: px-to-rem(14);
    }
  }
}
