.card-v {
  $origin: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;

  @include simple-property-DF(padding, 32);

  &__container {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: start;
    gap: 24px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 28, 26, 24, 22, 20);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);

    & i {
      color: var(--primary);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 18, 18, 16, 16, 14);
    line-height: 130%;
    color: var(--text-talee-light);

    & i {
      font-weight: 700;
      color: var(--primary);
    }

    & b {
    }
  }

  &__block {
  }
}
