.form {
  $origin: &;

  @include simple-property-DF(padding, 24, 24);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;

  height: 100%;

  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;

  }

  @include toPhone {
    &__container {
      display: flex;
      flex-direction: column;
    }
  }
  
  &--form-1 {
    #{$origin}__container {
      @include toLaptop {
        display: flex;
        flex-direction: column;
      }
    }
  }
}
