.title-easy-4 {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;
    line-height: 130%;
    text-transform: uppercase;

    @include simple-property-DF(font-size, 24, 24, 22, 22, 20);
    @include setAbsoluteColor(var(--text-talee-title));

    margin-bottom: 12px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;
    line-height: 120%;

    @include simple-property-DF(font-size, 22, 22, 20, 20, 18);
    @include setAbsoluteColor(var(--text-talee-light));
    @include text-ellipsis-lines;
  }

  &__container {
  }
}
