.card-block-9 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 16px;

  &__image {
    border-radius: 20px;
    width: 256px;
    height: 246px;
    object-fit: cover;

    @include toPhone {
      border-radius: 10px;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 400;
  
    @include simple-property-DF(font-size, 22, 22, 20, 18, 16);

    line-height: 120%;
    text-align: center;
    color: var(--text-talee-light);

    & b {
      font-weight: 700;
      line-height: 130%;
      color: var(--primary);
    }
  }
}
