.card-easy-6 {
  $origin: &;
  position: relative;

  @include simple-property-DF(padding, 40, 30);
  @include flex-center;
  background-color: var(--white);

  &__number {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(15);
    line-height: 130%;
    text-align: center;
    color: var(--primary-color-normal);

    position: absolute;
    top: 0;
    left: 0;

    @include simple-property-DF(padding, 40, 20);
  }

  &__container {
    @include flex-center;
    @include simple-property-DF(gap, 18);
  }

  &__image {
    $size: 128px;
    @include min-size($size);
    @include size($size);
  }

  &__title {
    @include simple-property-DF(font-size, 28, 20);
    font-family: var(--font-family);
    font-weight: 400;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-talee-title);
    width: 100%;
    max-width: 440px;
  }

  @include toDesktop {
    &__number {}

    &__container {}

    &__image {
      $size: 96px;
      @include min-size($size);
      @include size($size);
    }

    &__title {}
  }
}

