// @use "./breakpoints" as *;

@mixin onDesktop {
  @media (min-width: #{$lap + px}) {
    @content;
  }
}

@mixin onLaptop {
  @media (min-width: #{$tab + px}) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: #{$phab + px}) {
    @content;
  }
}

@mixin onPhablet {
  @media (min-width: #{$phone + px}) {
    @content;
  }
}

@mixin onPhone {
  @media (min-width: 0px) {
    @content;
  }
}
