.title-blue {
  display: flex;
  flex-direction: column-reverse;
  text-align: start;

  &__main-text {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 36, 32, 28, 26, 24);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-piro-dark);

    & b {
      font-weight: 500;
      color: var(--text-piro-accent);
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 18, 18, 16, 16, 14);
    line-height: 130%;
    color: var(--text-piro-light);
  }

  &__container {
  }
}
