.card-easy-12 {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  height: 100%;
  gap: 30px;
  
  @include simple-property-DF(gap, 30);
  @include simple-property-DF(padding, 44);

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__image {
    border-radius: 20px;
    width: 100%;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 36, 30, 28, 26, 24);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-piro-title);
    margin-bottom: 8px;
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 24, 22, 20, 18, 16);
    line-height: 130%;
    color: var(--text-piro-light);
  }

  &__block {
    order: -1;
    margin-bottom: 30px;
    height: 52px;
    
    @include toPhone {
      margin-bottom: 10px;
      height: 32px;
    }

    &__head {
      display: flex;
      align-items: center;

      &__number {
        font-family: var(--font-family);
        font-weight: 400;

        @include simple-property-DF(font-size, 48, 40, 36, 30, 28);
        line-height: 108%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: var(--second-material-300);

        margin-right: 50px;
      }

      &__line {
        height: 2px;
        background-color: var(--second-material-300);
        width: 100%;
        margin-right: -4px;
      }

      &__icon {
        @include flex-center;
      }
    }
  }
}
