.card-hard {
  max-height: 400px;

  &__head {
    display: flex;
    justify-content: space-between;

    @include simple-property-DF(padding, 40, 40);
    @include simple-property-DF(padding-bottom, 0);

    margin-bottom: 24px;

    &-container {
      display: flex;
      gap: 24px;
    }
  }

  &__image {
    @include min-size(102px);
    @include size(102px);
    position: relative;
    overflow: hidden;
    border: 2px solid var(--primary);
    border-radius: 100%;

    & img {
      @include size(108%);
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 28, 28, 22, 20, 18);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--text-tale-title);

    margin-bottom: 8px;
  }

  &__info {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    color: var(--text-talee-light);
  }

  &__body {
    @include simple-property-DF(padding, 40, 40);
    @include simple-property-DF(padding-top, 0);
    @include simple-property-DF(padding-right, 0);

    &-container {
      display: flex;
      justify-content: space-between;
      padding-right: 40px;

      margin-bottom: 8px;
    }
  }

  &__subtitle {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-light);
  }

  &__date {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: px-to-rem(18);
    line-height: 130%;
    text-align: right;
    color: var(--text-talee-light);
  }

  &__text {
    max-height: 100px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: px-to-rem(16);
    line-height: 130%;
    text-align: justify;
    color: var(--text-talee-dark);

    padding-right: 10px;
    margin-right: 10px;

    overflow-y: auto;

    @include size-scrollbar(20px);
  }

  // @include toPhone {
  //   &__head {}
  //   &__image {}
  //   &__title {}
  //   &__info {}
  //   &__body {}
  //   &__subtitle {}
  //   &__date {}
  //   &__text {}
  // }

  @include toPhone {
    &__head {
      flex-direction: column;
      // align-items: end;
      padding: 10px;
      padding-bottom: 0;
      margin-bottom: 0;
      
      &-container {
        width: 100%;
        justify-content: space-between;
        gap: 10px;
      }
    }


    &__image {
      @include size(70px);
      @include min-size(70px);
      border-width: 1px;
    }

    &__title {
      // font-size: px-to-rem(18);
    }

    &__info {}

    &__body {
      padding: 10px;
      padding-bottom: 20px;
    }

    &__subtitle {
      font-size: px-to-rem(16);
    }

    &__date {
      font-size: px-to-rem(16);
    }
    
    &__text {
      font-size: px-to-rem(14);
      @include size-scrollbar(4px);
    }
  }
}
