:root {
  @include toDesktop {
    --menu-gap: 18px;
  }

  @include toLaptop {
    --menu-gap: 16px;
  }

  @include toTablet {
    --menu-gap: 12px;
  }

  @include toPhone {
    --menu-gap: 8px;
    --menu-item: 40px;
  }
}

.contacts-menu {
  $origin: &;

  @include toDesktop { }
  @include toLaptop { }
  @include toTablet { }
  
  @include toPhone {
    &__button {
      @include min-size(var(--menu-item));
      @include size(var(--menu-item));
    }
    
    &__item {
      @include min-size(var(--menu-item));
      @include size(var(--menu-item));
    }
  }
}
