.card-block-new {
  $origin: &;
  display: flex;
  flex-direction: column-reverse;
  position: relative;

  @include simple-property-DF(height, 700, 700, 650, 600, 440);

  // max-height: max-content;

  &__image {
    @include no-event;
    position: absolute;
    border-radius: 20px;
    overflow: hidden;
    width: 100%;

    height: 100%;
    object-fit: cover;
    z-index: -1;
  }

  &__title {
    @include simple-property-DF(font-size, 48, 44, 38, 30, 26);

    font-family: var(--font-family);
    font-weight: 400;
    line-height: 120%;
    text-transform: uppercase;
    color: #fff8f0;

    margin-bottom: 12px;
  }

  &__text {
    @include simple-property-DF(font-size, 24, 24, 22, 20, 16);

    font-family: var(--second-family);
    font-weight: 400;
    line-height: 130%;
    color: #fff8f0;

    @include text-ellipsis-lines;
    margin-bottom: 30px;
  }

  &__container {
    @include simple-property-DF(padding, 64);

    position: relative;
    z-index: 1;
    width: 100%;
    max-width: 772px;

    &::after {
      content: attr(data-content);
      display: block;
      position: absolute;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: px-to-rem(20);
      line-height: 120%;
      color: var(--primary-surface-white);
      z-index: 3;

      @include simple-property-DF(top, -32);
      @include simple-property-DF(left, 64);
      padding-left: 30px;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      z-index: 2;

      @include size(24px);
      background-image: url("../images/icons/calendar.svg");

      @include simple-property-DF(top, -32);
      @include simple-property-DF(left, 64);
    }
  }

  &--normal {
    @include simple-property-DF(height, 556, 556, 520, 500, 350);

    #{$origin}__title {
      @include simple-property-DF(font-size, 36, 36, 32, 28, 22);
    }
    
    #{$origin}__text {
      @include simple-property-DF(font-size, 22, 22, 18, 16, 16);
    }

    #{$origin}__container {
      @include simple-property-DF(padding, 40);
      max-width: 100%;
      
      &::after {
        @include simple-property-DF(top, -32);
        @include simple-property-DF(left, 40);
      }
      
      &::before {
        @include simple-property-DF(top, -32);
        @include simple-property-DF(left, 40);
      }
    }
  }
}
