$transition-time: 0.5s;

.burger-menu {
  $origin: &;
  position: fixed;
  inset: 0;
  z-index: 101;
  overflow: hidden auto;

  transition: all $transition-time;

  -webkit-backdrop-filter: blur(45px);
  backdrop-filter: blur(45px);
  background: rgb(93 89 85 / 25%);

  &__container {
    position: relative;
    display: grid;
    grid-template-rows: max-content 1fr max-content;

    width: 100%;
    max-width: 1608px;
    min-height: 744px;
    border-radius: 0 0 40px;
    padding: 30px;

    -webkit-backdrop-filter: blur(16px);
    backdrop-filter: blur(16px);
    background: rgb(255 248 240 / 95%);

    transition: all $transition-time ease;

    @include toPhone {
      padding: 10px;
    }
  }

  &--closed {
    -webkit-backdrop-filter: blur(0);
    backdrop-filter: blur(0);
    background: transparent;

    #{$origin}__container {
      transform: translateX(-50%);
      opacity: 0;
    }
  }

  // Other

  &__decor {
    position: absolute;
  }

  &__header {
    display: grid;
    grid-template-columns: 1fr max-content;
    align-items: center;
    gap: 120px;
    margin-bottom: 46px;

    @include toTablet {
      gap: 20px;
    }
  }

  &__list-card {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 20px;

    @include toPhone {
      grid-template-columns: repeat(auto-fit, minmax(252px, 1fr));
    }
  }

  &__main {
    &__container {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 20px;
      justify-content: center;
      align-items: center;
      place-items: center;
      padding-block: 20px;
    }

    @include toDesktop {
      &__container {
        grid-template-columns: repeat(5, 1fr);
        gap: 10px;
      }
    }

    @include toLaptop {
      &__container {
        grid-template-columns: repeat(4, 1fr);
        gap: 5px;
      }

      &__list {
        grid-column: 1 / 5;
      }
    }

    @include toTablet {
      &__container {
        grid-template-columns: repeat(2, 1fr);
      }

      &__list {
        grid-column: 1 / 3;
      }
    }

    @include toPhone {
      &__container {
        display: flex;
        flex-direction: column;
        gap: 10px;

        & > * {
          place-self: start;
        }

        & > *:nth-child(2n) {
          place-self: end;
        }
      }
      
      &__list {
        place-self: center; 
      }
    }

    &__container-2 {
      height: 100%;
      display: flex;
      gap: 42px;
      padding-bottom: 30px;

      @include toTablet {
        flex-direction: column;
      }
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;

    &__author {
      display: flex;
      gap: 4px;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: px-to-rem(15);
      color: #5d5955;
    }

    &__container {
      display: flex;
      gap: 36px;
    }

    &__back {
      display: flex;
      gap: 8px;
      align-items: center;
      cursor: pointer;

      &__text {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: px-to-rem(14);
        line-height: 80%;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: var(--dark-gray);
      }
    }

    &__link {

    }

    @include toPhone {
      flex-direction: column-reverse;
      gap: 20px;

      &__container {
        flex-direction: row-reverse;
      }

      &__link {
        display: none;
      }
    }
  }
}
